import React,{Component} from 'react'

class User extends Component{
    constructor(props){
        super(props)
        this.state={popup:false}

    this.change=this.change.bind(this)
    this.close=this.close.bind(this)
    this.order=this.order.bind(this)
    }
    change(){
        console.log('yes')
        this.setState({popup:'yes'})
    }
    close(){
        this.setState({popup:'no'})
    }
    order(){
        this.setState({popup:'order'})
    }
    render(){
        return(
            <div>{
                (this.state.popup)
                ?<div>
                <div id='outer'>
                    <div id='inner'>
                        <button onClick={this.order}>ViewOrders</button><br/><br/><br/>
                        <button>LogOut</button><br/><br/><br/>
                        <button onClick={this.close}>Close</button><br/><br/>
                    </div>
                    </div>
                    {this.props.children}
                </div>
                :(<div>{this.props.children}
                    <div id='main'><button id='button' onClick={this.change}>User</button></div>
                
                </div>
                )
            }
            <style jsx>
            {`p{
                font-family:sans-serif;
                font-size:1em;
            }
            #outer{
                position:absolute;
                width:100%;
                height: 100%;
                top:0;
                bottom: 0;
                left:0;
                right:0;
                background-color:rgb(0,0,0,0.2);
            }
            #inner{
                padding-bottom:4em;
                padding-top:3em;
                position: fixed;
                left: 44%;
                right: 44%;
                top: 25%;
                bottom: 25%;
                height: 15%;
                margin: auto;
                background: white;
               text-align:center;
            }
            #button{
                position:absolute;
                margin-top:10em;
                 margin-left:110em;
                 cursor:pointer;
             }
            button{
                font-size:1em;
            }
            #main{position:fixed;}
            
            `}
            </style>
            </div>
        )
    }
}

export default User


